  .slider-settings{
        button{
            color: var(--primary-color)!important;
        }
        .components-button.is-primary{
            background-color: var(--white-color)!important;
            border: 1px solid var(--primary-color)!important;
            border-radius: 25px!important;
        }
  }
  
